html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Rubik", sans-serif;
  font-size: 16px;
  font-weight: 400;
  overflow-x: hidden;
}

header {
  background: url("../../../src/images/pattern-bg.png") no-repeat;
  background-position: center top;
  background-size: cover;
  height: 19em;
}
@media (min-width: 40em) {
  header {
    height: 16.5em;
  }
}

.header-wrapper {
  position: relative;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.header-wrapper .heading {
  text-align: center;
  font-size: 1.4rem;
  color: #fff;
  margin: 1em;
  font-weight: 500;
}
@media (min-width: 40em) {
  .header-wrapper .heading {
    font-size: 1.813rem;
  }
}
.header-wrapper .search-wrapper {
  display: flex;
  flex-direction: row;
  width: min(550px, 88%);
}
.header-wrapper .search-wrapper .address-input {
  color: #2b2b2b;
  cursor: pointer;
  border: none;
  border-top-left-radius: 15px;
  border-bottom-left-radius: 15px;
  width: 100%;
  height: 50px;
  padding: 0 25px;
  font-family: inherit;
  font-size: 1.125rem;
  outline: none;
  flex: 1;
}
@media (min-width: 40em) {
  .header-wrapper .search-wrapper .address-input {
    font-size: 1.1rem;
  }
}
.header-wrapper .search-wrapper .search-btn {
  background-color: black;
  border: none;
  width: 57px;
  border-top-right-radius: 15px;
  border-bottom-right-radius: 15px;
  cursor: pointer;
  transition: all 0.4s ease;
}
@media (hover: hover) and (pointer: fine) {
  .header-wrapper .search-wrapper .search-btn:hover {
    background-color: #2b2b2b;
  }
}
.header-wrapper .error {
  color: #f46262;
  font-size: 0.875rem;
  font-weight: 500;
  transform: translateY(-9px);
  transition: all 0.4s ease;
  margin: 0.5em;
  opacity: 0;
}
.header-wrapper .error.visible {
  display: block;
  opacity: 1;
  transform: translateY(-2px);
}
.header-wrapper .info-wrapper {
  position: absolute;
  width: min(76.875em, 88%);
  z-index: 999;
  text-align: center;
  left: 50%;
  bottom: 0;
  transform: translate(-50%, 50%);
  background-color: white;
  border-radius: 15px;
  padding: 25px;
  display: grid;
  row-gap: 20px;
}
@media (min-width: 40em) {
  .header-wrapper .info-wrapper {
    text-align: left;
    column-gap: 22px;
    row-gap: 7px;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: 1fr 1fr;
  }
}
@media (min-width: 64em) {
  .header-wrapper .info-wrapper {
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: 1fr;
  }
}
@media (min-width: 40em) {
  .header-wrapper .info-wrapper .info-box:nth-child(odd) {
    border-right: 1px solid #dedede;
  }
}
@media (min-width: 64em) {
  .header-wrapper .info-wrapper .info-box:not(:last-child) {
    border-right: 1px solid #dedede;
  }
}
.header-wrapper .info-wrapper .info-box .info-title {
  margin: 0;
  color: #969696;
  font-size: 0.6rem;
  font-weight: 700;
  letter-spacing: 1.5px;
}
@media (min-width: 40em) {
  .header-wrapper .info-wrapper .info-box .info-title {
    font-size: 0.7rem;
  }
}
.header-wrapper .info-wrapper .info-box .info-text {
  margin: 5px 0 0;
  font-size: 1.125rem;
  font-weight: 500;
  word-break: break-word;
}
@media (min-width: 64em) {
  .header-wrapper .info-wrapper .info-box .info-text {
    font-size: 1.375rem;
  }
}

.map-wrapper {
  width: 100%;
  height: 100vh;
}
@media (min-width: 60em) {
  .map-wrapper {
    height: calc(100vh - 16.5em);
  }
}
.map-wrapper #map {
  width: 100%;
  height: 100%;
}